"use client";

import { useState, useEffect } from "react";
import { Card } from "@/components/ui/card";

interface LocationRecord {
  latitude: number;
  longitude: number;
  accuracy: number;
  timestamp: number;
  photo: string;
  deviceInfo: {
    userAgent: string;
    platform: string;
    vendor: string;
    language: string;
  };
  networkInfo: {
    ipAddress: string;
    provider: string;
    country: string;
    city: string;
    region: string;
  };
}

export default function AdminPage() {
  const [locations, setLocations] = useState<LocationRecord[]>([]);

  useEffect(() => {
    const fetchLocations = async () => {
      try {
        const response = await fetch("/api/track-location");
        if (!response.ok) {
          throw new Error("Failed to fetch locations");
        }
        const data = await response.json();
        setLocations(data.history || []);
      } catch (err) {
        console.error("Error fetching locations:", err);
        setLocations([]);
      }
    };

    fetchLocations();

    // Set up polling to refresh data every 30 seconds
    const interval = setInterval(fetchLocations, 30000);
    return () => clearInterval(interval);
  }, []);

  const formatDate = (timestamp: number) => {
    return new Date(timestamp).toLocaleString();
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-100 to-gray-200 py-6 px-4">
      <div className="max-w-4xl mx-auto">
        <Card className="p-6 bg-white shadow-2xl rounded-xl border-0">
          <div>
            <div className="flex items-center justify-between mb-6">
              <h1 className="text-2xl font-bold text-gray-900">
                Location Tracking Admin
              </h1>
              <div className="text-sm text-gray-500">
                Total Records: {locations.length}
              </div>
            </div>

            <div className="space-y-4">
              {Array.isArray(locations) && locations.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {locations.map((record, index) => (
                    <div key={index} className="border rounded-lg p-4 bg-gray-50 hover:shadow-md transition-shadow">
                      <div className="flex gap-4">
                        <div className="w-1/3">
                          <div className="aspect-[3/4] relative rounded-md overflow-hidden bg-gray-200">
                            <img 
                              src={record.photo} 
                              alt="Location photo" 
                              className="absolute inset-0 w-full h-full object-cover"
                            />
                          </div>
                        </div>
                        <div className="flex-1 space-y-2">
                          <p className="text-sm text-gray-600">
                            <span className="font-medium">Time:</span><br/>
                            {formatDate(record.timestamp)}
                          </p>
                          <p className="text-sm text-gray-600">
                            <span className="font-medium">Coordinates:</span><br/>
                            <span className="font-mono">
                              {record.latitude.toFixed(6)}, {record.longitude.toFixed(6)}
                            </span>
                          </p>
                          <p className="text-sm text-gray-600">
                            <span className="font-medium">Accuracy:</span><br/>
                            {record.accuracy.toFixed(2)} meters
                          </p>
                          <div className="pt-2 border-t">
                            <p className="text-sm text-gray-600">
                              <span className="font-medium">Device:</span> {record.deviceInfo.platform}
                            </p>
                            <p className="text-sm text-gray-600">
                              <span className="font-medium">IP:</span> {record.networkInfo.ipAddress}
                            </p>
                            <p className="text-sm text-gray-600">
                              <span className="font-medium">Provider:</span> {record.networkInfo.provider}
                            </p>
                            <p className="text-sm text-gray-600">
                              <span className="font-medium">Location:</span> {record.networkInfo.city}, {record.networkInfo.region}, {record.networkInfo.country}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-12 text-gray-500">
                  No location history available
                </div>
              )}
            </div>
          </div>
        </Card>
      </div>
    </main>
  );
}
